.navbar {
	width: 100%;
	background-color: #171717;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 1.5rem 1rem;
	z-index: 50;
}

.navbarLogo h2 {
	color: #f4f4f4;
	font-size: 1.4rem;
	cursor: pointer;
}

.navbarLinks {
	display: flex;
	list-style: none;
	align-items: center;
}

.navbarLinks > li {
	padding-left: 1.5rem;
}

.navbarLinks > li > a {
	text-decoration: none;
	color: #f4f4f4;
	font-size: 1.2rem;
	display: flex;
	align-items: center;
}

.navbarLinks > li > a > span {
	display: flex;
	align-items: center;
	margin-left: 8px;
}

.cart__link {
	background: #333;
	padding: 10px;
	border-radius: 8px;
}

.cart__link:hover {
	background: #dd219e;
	color: #f4f4f4;
}

.cartlogoBadge {
	width: 30px;
	height: 30px;
	background-color: #f4f4f4;
	border-radius: 50%;
	margin-left: 8px;
	color: #171717;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 1rem;
}

.hamburgermenu {
	display: none;
	width: 30px;
	height: 30px;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	cursor: pointer;
}

.hamburgermenu:hover > div {
	background: #dd219e;
}

.hamburgermenu > div {
	width: 100%;
	height: 3px;
	background: #f4f4f4;
}

@media (max-width: 960px) {
	.navbarLinks {
		display: none;
	}

	.hamburgermenu {
		display: flex;
	}
}

@media (max-width: 500px) {
	.navbarLogo h2 {
		font-size: 1rem;
	}
}
